<template>
    <div class="content-wrap">
        <div class="search-wrap">
            <el-row
                type="flex"
                :gutter="10"
                style="height:52px;"
            >
                <el-col :span="5" >
                    <span class="item-text">云机编号：</span>
                    <el-input
                        class="items"
                        placeholder="请输入云机编号"
                        v-model="padCode" />
                </el-col>
                <el-col :span="5">
                    <span class="item-text">云机名称：</span>
                    <el-input
                        class="items"
                        placeholder="请输入云机名称"
                        v-model="userPadName" />
                </el-col>
                <el-col :span="5">
                    <span class="item-text">所属用户：</span>
                    <el-input
                        class="items"
                        placeholder="请输入手机号码"
                        v-model="telephone" />
                </el-col>
                <el-col :span="5">
                    <span class="item-text">序列号：</span>
                    <el-input
                        class="items"
                        placeholder="请输入序列号"
                        v-model="serialNum" />
                </el-col>
            </el-row>
            <el-row 
                type="flex"
                :gutter="10"
                style="height:52px;">
                <el-col :span="5">
                    <span class="item-text">云机状态：</span>
                    <el-select class="items" v-model="padStatusStr" placeholder="请选择">
                        <el-option
                            v-for="item in statusOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="5">
                    <span class="item-text">连接中：</span>
                    <el-select class="items" v-model="isConnect" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="5">
                    <span class="item-text">绑定时间：</span>
                    <SelectTime class="items" ref="selectBindTime" @selectTime="selectBindTime" :type="'datetimerange'" />
                </el-col>
                <el-col :span="5">
                    <span class="item-text">到期时间：</span>
                    <SelectTime class="items" ref="selectExpireTime" @selectTime="selectExpireTime" :type="'datetimerange'" />
                </el-col>
                <el-button type="primary" style="height:40px" @click="search">查询</el-button>
                <el-button style="height:40px" @click="reset">重置</el-button>
            </el-row>
        </div>
        <div class="table-wrap">
            <el-table
                :data="tableData"
                :header-cell-style="{textAlign: 'center'}"
                :cell-style="{ textAlign: 'center' }"
                border
                style="width: 100%">
                    <el-table-column
                        prop="pad_code"
                        label="云机编号"
                        width="180">
                    </el-table-column>
                    <el-table-column
                        prop="user_pad_name"
                        label="云机名称"
                        width="180">
                    </el-table-column>
                    <el-table-column
                        prop="pad_status_str"
                        label="云机状态">
                    </el-table-column>
                    <el-table-column
                        prop="bind_mobile"
                        label="所属用户">
                    </el-table-column>
                    <el-table-column
                        prop="group_name"
                        label="所属分组">
                    </el-table-column>
                    <el-table-column
                        label="绑定时间">
                        <template slot-scope="scope">
                            <span>{{scope.row.bind_time ? getTime(new Date(scope.row.bind_time)) : '-'}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="到期时间">
                        <template slot-scope="scope">
                            <span>{{scope.row.expire_time ? getTime(new Date(scope.row.expire_time)) : '-'}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="install_app_count"
                        label="安装应用数">
                    </el-table-column>
                    <el-table-column
                        prop="file_count"
                        label="文件分发数">
                    </el-table-column>
                    <el-table-column
                        label="连接中">
                        <template slot-scope="scope">
                            <span>{{scope.row.pad_status == 2 ? '是' : '否'}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        fixed="right"
                        label="操作">
                        <template slot-scope="scope">
							<el-button
								type="text"
								size="medium"
								@click="handleDetail(scope.row)"
								>查看</el-button
							>
                            <el-dropdown trigger="click" placement="bottom">
                                <span class="el-dropdown-link">
                                    更多<i class="el-icon-arrow-down el-icon--right"></i>
                                </span>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item><p @click="rebootPhone(scope.row)">重启云机</p></el-dropdown-item>
                                    <el-dropdown-item><p @click="resetPhone(scope.row)">恢复出厂</p></el-dropdown-item>
                                    <el-dropdown-item><p>连接云机</p></el-dropdown-item>
                                    <el-dropdown-item><p @click="showEditName(scope.row)">修改云机名</p></el-dropdown-item>
                                    <el-dropdown-item><p @click="bindPhone(scope.row)">设备绑定</p></el-dropdown-item>
                                    <el-dropdown-item><p @click="relievePhone(scope.row)">设备解绑</p></el-dropdown-item>
                                    <el-dropdown-item><p @click="addTimeVisible=true;columnData=scope.row;">延长到期时间</p></el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
						</template>
                    </el-table-column>
            </el-table>
            <Pagination 
                style="margin:10px 0 20px 0;text-align:right;"
                @handleCurrentChange="handleCurrentChange"
                @handleSizeChange="handleSizeChange"
                :total="totalNum"
                :currentPage="currentPage"
                :pageSize="pageSize"
            />
        </div>
        <el-dialog
            title="修改云机名"
            :before-close="handleModalClose"
            :visible.sync="updatePadNameVisible"
            :close-on-click-modal='false'
            width="30%">
            <el-input
                placeholder="请输入云机名"
                @input="getByteLen"
                autocomplete="off"
                v-model="padName" />
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleModalClose()">取 消</el-button>
                <el-button type="primary" @click="editPadName()">确 定</el-button>
            </span>
        </el-dialog>
        <el-dialog
            title="绑定云机"
            :before-close="handleModalClose"
            :visible.sync="bindVisible"
            :close-on-click-modal='false'
            width="30%">
            <el-row
                style="height:52px;"
            >
                <el-col>
                    <span class="item-text">手机号:</span>
                    <el-input
                        class="items"
                        placeholder="请输入手机号"
                        @input="getByteLen"
                        autocomplete="off"
                        v-model="phoneNum" />
                </el-col>
            </el-row>
            <el-row
                style="height:52px;"
            >
                <el-col>
                    <span class="item-text">设备绑定:</span>
                    <el-select class="items" v-model="goodsId" placeholder="请选择套餐类型">
                        <el-option
                            v-for="item in goodsList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleModalClose()">取 消</el-button>
                <el-button type="primary" @click="bind()">确 定</el-button>
            </span>
        </el-dialog>
        <el-dialog
            title="延长到期时间"
            :before-close="handleModalClose"
            :visible.sync="addTimeVisible"
            :close-on-click-modal='false'
            width="30%">
            <el-radio-group v-model="time">
                <el-radio-button label="1天"></el-radio-button>
                <el-radio-button label="5天"></el-radio-button>
                <el-radio-button label="30天"></el-radio-button>
                <el-radio-button label="90天"></el-radio-button>
                <el-radio-button label="180天"></el-radio-button>
                <el-radio-button label="365天"></el-radio-button>
            </el-radio-group>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleModalClose()">取 消</el-button>
                <el-button type="primary" @click="addTime()">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import { padList, updatePadName, bindPad, relievePad, addExpireTime, rebootPads, resetPads, padGoodsList } from '../../api/phone';
import { parseTime } from '../../utils/index';
import { Pagination, SelectTime } from '../../components'
export default {
    data() {
        return{
            padCode: '',
            userPadName: '',
            padName: '',
            telephone: '',
            serialNum: '',
            bindBeginTime: '',
            bindEndTime: '',
            expireBeginTime: '',
            expireEndTime: '',
            padStatusStr: '',
            statusOptions: [
                {
                    value: '空闲',
                    label: '空闲'
                }, {
                    value: '在线',
                    label: '在线'
                }, {
                    value: '到期',
                    label: '到期'
                }, {
                    value: '故障',
                    label: '故障'
                }
            ],
            isConnect: '',
            options: [
                {
                    value: 1,
                    label: '是'
                }, {
                    value: 0,
                    label: '否'
                }
            ],
            tableData: [],
            currentPage: 1,
            totalNum: 0,
            pageSize: 10,
            columnData: {},
            updatePadNameVisible: false,
            time: '1天',
            addTimeVisible: false,
            phoneNum: '',
            bindVisible: false,
            goodsId: '',
            goodsList: []
        }
    },
    // filters: {
    //     getTime(time) {
	// 		return parseTime(time);
	// 	},
    // },
    mounted() {
        this.getList();
    },
    methods: {
        getTime(time){
            return parseTime(time);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
        },
        getByteLen(val) {
            let data = this.padName;
            let len = 0;
            let cutstr = "";
            for (var i = 0; i < data.length; i++) {
                var a = val.charAt(i);
                if (a.match(/^[A-Za-z0-9_\-~!@#$%^&*()-=?><;'|]+$/) === null) {
                len += 2;
                if (len > 20) {
                    break;
                } else {
                    cutstr += a;
                }
                } else {
                len += 1;
                if (len > 20) {
                    break;
                } else {
                    cutstr += a;
                }
                }
            }
            this.padName = cutstr;
        },
        search() {
            this.currentPage = 1;
            this.pageSize = 10;
            this.getList()
        },
        reset() {
            this.appName = '';
            this.packageName  = "";
            this.telephone = '';
            this.serialNum = '';
            this.padStatusStr = '';
            this.isConnect = '';
            this.currentPage = 1;
            this.pageSize = 10;
            this.$refs.selectBindTime.resetTimes();
            this.$refs.selectExpireTime.resetTimes();
            this.getList();
        },
        selectBindTime(val) {
            this.bindBeginTime = val.startTime;
            this.bindEndTime = val.endTime
        },
        selectExpireTime(val) {
            this.expireBeginTime = val.startTime;
            this.expireEndTime = val.endTime
        },
        getList() {
            let params = new Object();
            params.padCode = this.padCode;
            params.userPadName  = this.userPadName;
            params.telephone = this.telephone;
            params.serialNum = this.serialNum;
            params.padStatusStr = this.padStatusStr;
            this.bindBeginTime ? params.bindBeginTime = this.bindBeginTime : null;
            this.bindEndTime ? params.bindEndTime = this.bindEndTime : null;
            this.expireBeginTime ? params.expireBeginTime = this.expireBeginTime : null;
            this.expireEndTime ? params.expireEndTime = this.expireEndTime : null;
            params.isConnect = this.isConnect;
            params.pageNum = this.currentPage;
            params.pageSize = this.pageSize;
            padList(params)
            .then(res => {
                if (res.resultCode == 0) {
                    this.tableData = res.resultInfo.userPadList;
                    this.totalNum = res.resultInfo.total;
                }
            })
            .catch(error => {
                console.log(error)
            });
        },
        handleDetail(v) {
            this.$store.dispatch('phone/selectedPhone', v);
            this.$router.push('/phone/phone-detail');
        },
        rebootPhone( v){
            this.$confirm("确定要重启该云机吗", "", {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.reboot(v)
            }).catch(() => {         
            });
        },
        reboot(v){
            let params = new Object();
            params.padCodes = v.pad_code;
            rebootPads(params)
            .then((res) => {
                if (res.resultCode == 0) {
                    this.$message({
                        message: "设备重启中，预计需要1-2分钟",
                        type: "success",
                    });
                } else if(res.res == 1){
                    this.$message({
                        message: res.error,
                        type: "warning",
                    });
                }else{
                    this.$message({
                        message: res.resultInfo,
                        type: "warning",
                    });
                }
            })
            .catch((error) => {});
        },
        resetPhone(v){
            this.$confirm("恢复出厂设置，将会清空该云机的所有数据，是否恢复？", "", {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.resetPad(v)
            }).catch(() => {         
            });
        },
        resetPad(v){
            let params = new Object();
            params.padCodes = v.pad_code;
            resetPads(params)
            .then((res) => {
                if (res.resultCode == 0) {
                    this.$message({
                        message: "设备重置中，预计需要1-2分钟",
                        type: "success",
                    });
                } else if(res.res == 1){
                    this.$message({
                        message: res.error,
                        type: "warning",
                    });
                }else{
                    this.$message({
                        message: res.resultInfo,
                        type: "warning",
                    });
                }
            })
            .catch((error) => {});
        },
        getGoodsList() {
            padGoodsList().then((res) => {
                if (res.resultCode == 0) {
                    let data = res.resultInfo;
                    let arr = [];
                    data.map(v=>{
                        let obj = {}
                        obj.label = v.goods_name;
                        obj.value = v.goods_id;
                        arr.push(obj)
                    })
                    this.goodsList = arr;
                } 
            })
            .catch((error) => {});
        },
        bindPhone(v){
            this.getGoodsList();
            this.columnData = v;
            this.bindVisible = true;
        },
        bind(){
            let reg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
            if (this.phoneNum == "") {
                this.$message({
                    message: "请输入手机号",
                    type: "warning",
                });
                return;
            }
            if (!reg.test(this.phoneNum)) {
                this.$message({
                    message: "请输入正确的手机号",
                    type: "warning",
                });
                return;
            }
            if (this.goodsId == "") {
                this.$message({
                    message: "请选择套餐类型!",
                    type: "warning",
                });
                return;
            }
            let params = new Object();
            params.padId = this.columnData.pad_id;
            params.goodsId = this.goodsId;
            params.telephone = this.phoneNum;
            bindPad(params)
            .then((res) => {
                if (res.resultCode == 0) {
                    this.$message({
                        message: res.resultInfo,
                    });
                }else{
                    this.$message({
                        message: res.resultInfo,
                        type: "warning",
                    });
                }
                this.goodsList();
                this.handleModalClose();
            })
            .catch((error) => {});
        },
        relievePhone(v) {
            this.$confirm("确定要解绑该云机？", "", {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.relieve(v)
            }).catch(() => {         
            });
        },
        relieve(v) {
            console.log
            let params = new Object();
            params.padIds = v.pad_id;
            relievePad(params)
            .then((res) => {
                if (res.resultCode == 0) {
                    this.$message({
                        message: "解绑成功！",
                        type: "success",
                    });
                } else if(res.res == 1){
                    this.$message({
                        message: res.error,
                        type: "warning",
                    });
                }else{
                    this.$message({
                        message: res.resultInfo,
                        type: "warning",
                    });
                }
            })
            .catch((error) => {});
        },
        showEditName(v){
            console.log(v)
            this.updatePadNameVisible = true;
            this.columnData = v;
            this.padName = v.user_pad_name;
        },
        handleModalClose() {
            this.updatePadNameVisible = false;
            this.addTimeVisible = false;
            this.bindVisible = false;
            this.columnData = '';
            this.padName = '';
            this.time = '1天';
            this.phoneNum = '';
            this.goodsId = '';
        },
        editPadName() {
            let reg = /^[A-Za-z0-9_\-\u4e00-\u9fa5]+$/;
            if (this.padName.length == "") {
                this.$message({
                    message: "请输入设备名称!",
                    type: "warning",
                });
                return;
            }
            if (!reg.test(this.padName)) {
                this.$message({
                    message: "设备名称不支持输入特殊字符！",
                    type: "warning",
                });
                return;
            }
            let params = new Object();
            params.padCode = this.columnData.pad_code;
            params.userId = this.columnData.user_id;
            params.padName = this.padName;
            updatePadName(params)
            .then((res) => {
                if (res.resultCode == 0) {
                    this.$message({
                        message: "修改云机名称成功",
                        type: "success",
                    });
                    this.getList();
                }else{
                    this.$message({
                        message: res.resultInfo,
                        type: "warning",
                    });
                }
                this.updatePadNameVisible = false;
                this.padName = "";
                this.columnData = "";
            })
            .catch((error) => {});
        },
        addTime() {
            let params = new Object();
            console.log(this.time)
            switch(this.time) {
                case '1天':
                    params.expireTime = new Date(this.columnData.expire_time).getTime() + 24*60*60*1000;
                    break;
                case '5天':
                    params.expireTime = new Date(this.columnData.expire_time).getTime() + 5*24*60*60*1000;
                    break;
                case '30天':
                    params.expireTime = new Date(this.columnData.expire_time).getTime() + 30*24*60*60*1000;
                    break;
                case '90天':
                    params.expireTime = new Date(this.columnData.expire_time).getTime() + 90*24*60*60*1000;
                    break;
                case '180天':
                    params.expireTime = new Date(this.columnData.expire_time).getTime() + 180*24*60*60*1000;
                    break;
                case '365天':
                    params.expireTime = new Date(this.columnData.expire_time).getTime() + 365*24*60*60*1000;
                    break;
            };
            params.userId = this.columnData.user_id;
            params.padCode = this.columnData.pad_code;
            addExpireTime(params)
            .then((res) => {
                if (res.resultCode == 0) {
                    this.$message({
                        message: "延长到期时间成功！",
                        type: "success",
                    });
                    this.getList();
                }else{
                    this.$message({
                        message: res.resultInfo,
                        type: "warning",
                    });
                }
                this.addTimeVisible = false;
                this.time = "1天";
                this.columnData=null;
            })
            .catch((error) => {});
        }
    },
    components: {
        Pagination, SelectTime
    }
}
</script>
<style lang="less" scoped>
.content-wrap{
    padding: 25px;
    .search-wrap,.el-dialog{
        padding: 20px;
        border: 1px solid #ddd;
        .items{
            width: calc(100% - 100px);
        }
        .item-text{
            width: 80px;
            text-align: left;
            display: inline-block;
        }
    }
    .table-wrap{
        margin-top: 20px;
    }
}
</style>


